<template>
  <div class="paginator">
    <span>Количество элементов на странице: </span>
    <select name=""
            id="perpage"
            v-model.number="perPage"
            @change="changeShownItemsRange">
      <option v-for="(cnt, index) in rangeCountsShownItems" :value=cnt>{{cnt}}</option>
    </select>
    <span> {{from}} - {{to}} из </span>

    <button class="btn btn-pict btn-nav"
            title="Предыдущая страница"
            :disabled="curPage === 1"
            @click="prev">
      <img src="../assets/icons/png/arrow-prev.png"
           alt="Пред.">
    </button>
    <span>{{curPage}}</span>
    <button class="btn btn-pict btn-nav"
            title="Следующая страница"
            :disabled="to >= countItems"
            @click="next">
      <img src="../assets/icons/png/arrow-next.png"
           alt="След.">
    </button>
  </div>
</template>

<script>
  export default {
    name: "Paginator",
    props: ['items'],
    data() {
      return {
        curPage: 1,
        perPage: 5,
        rangeCountsShownItems: [5,10,25,50]
      }
    },
    //TODO: do obtaining perPage from parent
    beforeUpdate() {
      this.changePage();
    },
    computed: {
      countItems() {
        return this.items.length;
      },
      from() {
        if (this.countItems < this.curPage * this.perPage - this.perPage + 1) {
          this.curPage = 1;
        }
        return this.curPage * this.perPage - this.perPage + 1;
      },
      to() {
        return this.curPage * this.perPage;
      },
    },
    methods: {
      next() {
        this.curPage++;
        this.changePage();
      },
      prev() {
        this.curPage--;
        this.changePage();
      },
      changeShownItemsRange() {
        const maxPage = Math.ceil(this.countItems / this.perPage);
        if (this.curPage > maxPage) {
          this.curPage = maxPage;
        }
        this.changePage();
      },
      changePage() {
        const shownData = this.changeRangeData();
        this.$emit('change-page', shownData, this.from, this.to);
      },
      changeRangeData() {
        return this.items.slice(this.from - 1, this.to);
      }
    }
  }
</script>

<style scoped>
  button:disabled {
    opacity: 0.4;
  }
  .paginator {
    font-size: 12px;
  }
</style>


